.container {
  font-size: 24px;
}

.nav > ol {
  margin-bottom: 0;
}

.article {
  margin: 20px;
  margin-top: 0;
}

.btn {
  border: 0;
  padding: 0;
  text-align: center;
  background-color: #0000;
  color: gray;
}

.nav > .btn {
  font-size: 24px;
  margin-left: 40px;
}

.article > h2 {
  margin-top: 24px;
}

/* mobile */
@media (hover: none) and (pointer: coarse) {
  .top {
    display: grid;
    text-align: center;
    border-bottom: 1px solid gray;
    grid-template-columns: 0fr 1fr 1fr;
  }
  .article {
    height: 500px;
  }

  .title a {
    margin-left: 5px;
    font-size: 25px;
  }

  .top > .btn {
    justify-self: end;
    margin-top: auto;
    margin-bottom: auto;
    font-size: 12px;
    width: 40px;
    height: 40px;
    font-weight: bold;
    border-radius: 12px;
    margin-right: 10px;
  }
}

/* pc */
@media (hover: hover) and (pointer: fine) {
  .container {
    display: grid;
    grid-template-columns: 150px 1fr;
  }

  .top {
    display: grid;
    text-align: center;
    border-bottom: 1px solid gray;
    margin-right: 10px;
    grid-template-columns: 1fr 1fr 1fr;
  }

  .nav {
    border-right: 1px solid gray;
    height: 1000px;
  }

  .top > .btn {
    justify-self: end;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 20px;
    font-size: 12px;
    width: 40px;
    height: 40px;
    font-weight: bold;
    border-radius: 12px;
    margin-right: 10px;
  }
}
